import { useMemo } from "react";
import "./HotelCard1.css";

const HotelCard1 = ({
  accommodationImageUrl,
  accommodationType,
  accommodationName,
  accommodationPrice,
  accommodationVideoUrl,
  accommodationReviews,
  accommodationRating,
  accommodationIconUrl,
  propFlex,
  propGap,
  propWidth,
  propRight,
  openMatterhornPopup,
}) => {
  const stayDetailsStyle = useMemo(() => {
    return {
      flex: propFlex,
      gap: propGap,
    };
  }, [propFlex, propGap]);

  const vectorIconStyle = useMemo(() => {
    return {
      width: propWidth,
      right: propRight,
    };
  }, [propWidth, propRight]);

  return (
    <div className="hotel-card-1">
      <div className="hotel-card">
        <img
          className="matterhorn-suites-image"
          alt=""
          src={accommodationImageUrl}
        />
        <div className="stay-details" style={stayDetailsStyle}>
          <div className="stay-details-rows">
            <div className="entire-bungalow">{accommodationType}</div>
            <b className="matterhorn-suites">{accommodationName}</b>
            <div className="night">{accommodationPrice}</div>
          </div>
          <img
            className="video-icon"
            alt=""
            src={accommodationVideoUrl}
            onClick={openMatterhornPopup}
          />
        </div>
        <div className="rating">
          <div className="reviews">{accommodationReviews}</div>
          <div className="parent">
            <div className="div2">{accommodationRating}</div>
            <img
              className="vector-icon"
              alt=""
              src={accommodationIconUrl}
              style={vectorIconStyle}
            />
          </div>
        </div>
        <button className="more-details-button">
          <div className="button1">More details</div>
        </button>
      </div>
    </div>
  );
};

export default HotelCard1;
